import React, { Component } from 'react';
import styled from 'styled-components';

const TimebarWrapper = styled.div`
	font-size: 1.6rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	.seconds_s {
		text-transform: lowercase;
	}
`;

class timebar extends Component {
	handleTimerToggle = () => {
		if (this.props.timerIsPaused) {
			this.props.startTimer();
		} else {
			this.props.stopTimer();
		}
	};

	render() {
		return (
			<TimebarWrapper>
				<span>
					Time Remaining: {this.props.timer}
					<span className="seconds_s">s</span>
				</span>
				<span>
					{/* inline if statement needed to determine what icon to show */}
					<button onClick={this.handleTimerToggle}>
						{this.props.timerIsPaused ? 'Start' : 'Stop'}
					</button>
				</span>
			</TimebarWrapper>
		);
	}
}

export default timebar;
